import "./App.scss";
import { Routes, Route } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <header className="App-header">ddd</header>
      <Routes>
        <Route path="/" element={<h3>Home</h3>} />
        <Route path="/portfolio_2021" element={<h3>portfolio_2021</h3>} />
        <Route path="/popular" element={<h3>popular</h3>} />
      </Routes>
    </div>
  );
}

export default App;
